<template>
    <div> 
        <div class="formforfiltr">
        <div class="filters">
            <div class="filters">
                <div class="form-group">
                    <label></label>
                    <select id="brand" v-model="filters.brand">
                        <option value=""></option>
                        <option value="Toyota">Toyota</option>
                        <option value="BMW">BMW</option>
                        <option value="Mercedes-Benz">Mercedes-Benz</option>
                        <option value="Audi">Audi</option>
                        <option value="Tesla">Tesla</option>
                        <option value="Kia">Kia</option>
                        <option value="Ford">Ford</option>
                        <option value="Porsche">Porsche</option>
                    </select>
                </div>
                <div class="form-group">
                    <label></label>
                    <select v-model="filters.model">
                        <option value=""></option>
                        <option v-for="model in filteredModels" :value="model" :key="model">{{ model }}</option>
                    </select>
                </div>
                <div class="form-group">
                    <label></label>
                    <select v-model="filters.class">
                        <option value=""></option>
                        <option value="economy"></option>
                        <option value="luxury"></option>
                    </select>
                </div>
                <div class="form-group">
                    <label></label>
                    <select v-model="filters.condition">
                        <option value=""></option>
                        <option value="new"></option>
                        <option value="used">/</option>
                    </select>
                </div>
                <div class="form-group">
                    <label> </label>
                    <select v-model="filters.type">
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                    </select>
                </div>
                <div class="form-group">
                    <label> </label>
                    <input type="number" v-model="filters.priceFrom">
                </div>
                <div class="form-group">
                    <label> </label>
                    <input type="number" v-model="filters.priceTo">
                </div>
            </div>
        </div>
        </div>

        <div class="car-list">
            <div class="card" v-for="car in filteredCars" :key="car.id">
                <div class="car-slider">
                    <img :src="car.image" :alt="`${car.brand} ${car.model}`">
                </div>
                <h3>{{ car.brand }} {{ car.model }}</h3>
        <div class="car-description">
            <div class="description-item">
                <img src="/img/price.png" alt="Price Icon">
                <p>: {{ car.price }}  /  </p>
            </div>
            <div class="description-item">
                <img src="/img/owner.png" alt="Owner Icon">
                <p>: {{ car.owner }}</p>
            </div>
            <div class="description-item">
                <img src="/img/car.png" alt="Type Icon">
                <p>:
                {{ car.type }}
                </p>
                </div>
        </div>
            </div>
        </div>
    </div>
</template>

<style scoped>

body { font-family: 'NucleoIcons', sans-serif; margin: 0; padding: 0; }
header {color: white; text-align: center; padding: 10px; }
.container { padding: 20px; }
.card { border: 1px solid #ccc; padding: 15px; margin: 10px; border-radius: 5px; }
.card h3 {font-size: 24px;}
.card p {font-size: 22px; color: #67748e; font-weight: 600; text-align: left;}
.form-group { margin-bottom: 15px; }
.form-group label { display: block; margin-bottom: 5px; font-size: 0.875rem !important;}
.form-group input, .form-group select { width: 100%; padding: 8px; }
table { width: 100%; border-collapse: collapse; }
table th, table td { border: 1px solid #ddd; padding: 8px; text-align: center; }
table th { background-color: #f4f4f4; }
.filters { display: flex; flex-wrap: wrap; gap: 10px; margin-bottom: 20px; }
.filters .form-group { flex: 1; min-width: 150px; }
.car-slider img{width: 600px; height: 500px;object-fit: cover; border-radius: 8px; box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2); margin-bottom: 20px;}
.formforfiltr {
    margin: auto;
    border-radius: 20px;
    width: 1500px;

    background-color: rgb(255, 255, 255);
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    justify-items: auto;
}
.filters {
    margin-top: 20px;
    margin-bottom: 10px;
    margin-left: 50px;
}

.car-list {
    display: grid; /*   */
    grid-template-columns: repeat(4, 1fr); /* 4    */
    gap: 20px; /*    */
    margin-top: 20px;
}

.card {
    border: 1px solid #ccc;
    padding: 15px;
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    text-align: center;
    transition: transform 0.2s ease;
    background-color: white;
}

.card:hover {
    transform: translateY(-5px);
}

.card img {
    width: 100%;
    height: 200px;
    object-fit: cover;
    border-radius: 4px;
}

.car-description {
    margin-top: 15px;
}

.description-item {
    display: flex;
    align-items: center;
}

.description-item p{
    margin-bottom: 4px;
}

.description-item img {
    width: 40px; /*     */
    height: 40px;
    margin-right: 10px;
    margin-bottom: 5px;
    background: linear-gradient(135deg, #a3e635, #16a34a); /*   */
    padding: 5px; /*    */
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /*   */
}


</style>

<script>
import corolla from "@/assets/img/cars_photo/toyota.png";
import bmw from "@/assets/img/cars_photo/bmw.png";
import bmwx6 from "@/assets/img/cars_photo/x62.png";
import cls from "@/assets/img/cars_photo/cls63.png";
import audi from "@/assets/img/cars_photo/audi.png";
import m5 from "@/assets/img/cars_photo/m5.png";
import tesla from "@/assets/img/cars_photo/tesla.png";
import raptor from "@/assets/img/cars_photo/raptor.png";
import kia from "@/assets/img/cars_photo/kia.png";
import focus from "@/assets/img/cars_photo/focus.png";
import g63 from "@/assets/img/cars_photo/g63.png";
import panama from "@/assets/img/cars_photo/panama.png";


export default {
    name: " ",
    data() {
        return {
            filters: {
                brand: '',
                model: '',
                class: '',
                condition: '',
                type:'',
                priceFrom: '',
                priceTo: ''
            },
            cars: [
                { id: 1, brand: 'Toyota', model: 'Corolla', price: 5000, owner: ' .', class: 'economy', condition:'new' ,type: '', image: corolla },
                { id: 2, brand: 'BMW', model: 'X5', price: 15000, owner: ' .', class: 'luxury', condition:'used' ,type: '', image: bmw },
                { id: 3, brand: 'BMW', model: 'X6', price: 17500, owner: ' .', class: 'luxury', condition:'new' ,type: '', image: bmwx6 },
                { id: 4, brand: 'Mercedes-Benz', model: 'CLS 63 AMG', price: 21000, owner: ' .', class: 'luxury', condition:'used' ,type: '', image: cls },
                { id: 5, brand: 'Audi', model: 'Rs7', price: 12500, owner: ' .', class: 'luxury', condition:'new' ,type: '', image: audi },
                { id: 6, brand: 'BMW', model: 'M5', price: 45000, owner: ' .', class: 'luxury', condition:'used' ,type: '', image: m5 },
                { id: 7, brand: 'Tesla', model: 'Model Y', price: 35000, owner: ' .', class: 'luxury', condition:'used' ,type: '', image: tesla },
                { id: 8, brand: 'Ford', model: 'F-Series', price: 9500, owner: ' .', class: 'economy', condition:'new' ,type: '', image: raptor },
                { id: 9, brand: 'Kia', model: 'Rio', price: 2500, owner: ' .', class: 'economy', condition:'used' ,type: '', image: kia },
                { id: 10, brand: 'Ford', model: 'Focus', price: 7000, owner: ' .', class: 'economy', condition:'used' ,type: '', image: focus },
                { id: 11, brand: 'Mercedes-Benz', model: 'G63 AMG', price: 55000, owner: ' .', class: 'luxury', condition:'used' ,type: '', image: g63 },
                { id: 12, brand: 'Porsche', model: 'Panamera', price: 35000, owner: ' .', class: 'luxury', condition:'used' ,type: '', image: panama }
            ],
            allModels: {
            'Toyota': ['Corolla'],
            'BMW': ['X5', 'X6', 'M5'],
            'Mercedes-Benz': ['G63 AMG', 'CLS 63 AMG'],
            'Audi': ['Rs7'],
            'Tesla': ['Model Y'],
            'Kia': ['Rio'],
            'Ford': ['Focus', 'F-Series'],
            'Porsche': ['Panamera']
        }
        };
    },
    computed: {
        filteredModels() {
            if (this.filters.brand) {
                return this.allModels[this.filters.brand] || [];
            }
            return [];
        },
        filteredCars() {
            return this.cars.filter(car => {
                return (
                    (!this.filters.model || car.model.toLowerCase().includes(this.filters.model.toLowerCase())) &&
                    (!this.filters.brand || car.brand.toLowerCase().includes(this.filters.brand.toLowerCase())) &&
                    (!this.filters.class || car.class === this.filters.class) &&
                    (!this.filters.condition || car.condition === this.filters.condition) &&
                    (!this.filters.type || car.type === this.filters.type) &&
                    (!this.filters.priceFrom || car.price >= this.filters.priceFrom) &&
                    (!this.filters.priceTo || car.price <= this.filters.priceTo)
                );
            });
        }
    },
    methods: {
        updateModels() {
            this.filters.model = ''; //     
        }
    }
};
</script>


